@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@200;300&family=Work+Sans:wght@300&display=swap');
.App{
  min-height: 100vh;
  display: flex;
  background-image: url("./background.jpg");
  background-position: center;
  background-size: contain;
  color: black;
}

::-webkit-scrollbar{
  width: 0;
}

::-webkit-scrollbar-thumb {
  background: rgba(136, 136, 136, 0.281);
}

::-webkit-scrollbar-thumb:hover {
  background: #555;
}